@charset "UTF-8";
body {
  margin: 0;
}

@media (min-width: 320px) {
  .page-body {
    font-family: "Ubuntu", sans-serif;
    font-weight: 300;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
    position: relative;
  }
  .page-body::after {
    content: "";
    position: absolute;
    background-image: url(../../img/mobile-intro-bg.jpg);
    background-repeat: no-repeat;
    width: 320px;
    height: 568px;
    z-index: -1;
  }
}
@media (min-width: 320px) {
  .page-header {
    display: flex;
    width: 320px;
    padding-top: 22px;
    position: fixed;
    z-index: 3;
  }

  .page-header__logo {
    width: 28px;
  }

  .page-header__button {
    width: 108px;
    height: 34px;
  }

  .page-header__nav-toggle {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    position: relative;
    width: 24px;
    background-color: #FFFFFF;
    height: 2px;
    box-shadow: 0 6px 0 #FFFFFF;
    margin-left: 62px;
    margin-top: 7px;
  }
  .page-header__nav-toggle::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #FFFFFF;
    top: 12px;
    left: 8px;
  }

  .page-header--scrolled {
    background-color: #000000;
    padding-bottom: 20px;
    border-bottom: 1px solid #0DD1A2;
  }
}
@media (min-width: 320px) {
  .logo {
    background-image: url("../../img/digpl-logo.svg");
    background-repeat: no-repeat;
    margin-left: 20px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 320px) {
  .order-button {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    border: 2px solid #0DD1A2;
    border-radius: 2px;
    font-size: 14px;
    margin-left: 58px;
  }
}
@media (min-width: 320px) {
  .nav--closed {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (min-width: 320px) {
  .nav-list {
    list-style-type: none;
  }
}
@media (min-width: 320px) {
  .page-main {
    width: 320px;
    margin: 0 auto;
  }

  .page-main__wrapper {
    background-image: url("../../img/index-bg.jpg");
    background-attachment: fixed;
    background-size: cover;
    margin-top: -23px;
  }
}
@media (min-width: 320px) {
  .intro {
    width: 288px;
    margin: 146px auto 0 auto;
  }

  .intro__title {
    margin: 0;
    font-size: 36px;
    font-weight: 400;
    color: #FFFFFF;
    text-align: center;
  }

  .intro__description {
    margin: 20px 0 0 0;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px;
    color: #FFFFFF;
    text-align: center;
  }

  .intro__buttons {
    margin: 50px auto 0 auto;
  }

  .intro__scroll-button {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    width: 288px;
    margin-top: 57px;
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 2px;
    opacity: 0.5;
    position: relative;
  }
  .intro__scroll-button::before {
    content: "";
    width: 7px;
    height: 1px;
    position: absolute;
    background-color: #FFFFFF;
    opacity: 0.5;
    transform: rotate(45deg);
    top: 25px;
    left: 137px;
  }
  .intro__scroll-button::after {
    content: "";
    width: 7px;
    height: 1px;
    position: absolute;
    background-color: #FFFFFF;
    opacity: 0.5;
    transform: rotate(-45deg);
    top: 25px;
    left: 142px;
  }
}
@media (min-width: 320px) {
  .intro-buttons {
    display: flex;
    flex-direction: column;
    width: 207px;
  }

  .intro-buttons__order {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    width: 207px;
    height: 55px;
    background-color: #15B38D;
    position: relative;
    border-radius: 3px;
  }
  .intro-buttons__order::after {
    content: "Заказать";
    position: absolute;
    top: 18px;
    left: 72px;
    font-size: 18px;
    line-height: 21px;
    font-weight: 700;
  }
  .intro-buttons__order::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 35px;
    width: 25px;
    height: 25px;
    z-index: 1;
    background-image: url("../../img/order-btn-icon.svg");
    background-repeat: no-repeat;
  }

  .intro-buttons__video {
    margin-top: 12px;
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    width: 207px;
    height: 55px;
    border: 3px solid #15B38D;
    border-radius: 3px;
    position: relative;
  }
  .intro-buttons__video::after {
    content: "Видео";
    position: absolute;
    top: 15px;
    left: 88px;
    font-size: 18px;
    line-height: 21px;
    font-weight: 700;
  }
  .intro-buttons__video::before {
    content: "";
    position: absolute;
    top: 13px;
    left: 49px;
    width: 25px;
    height: 25px;
    z-index: 1;
    background-image: url("../../img/video-btn-icon.svg");
    background-repeat: no-repeat;
  }
}
@media (min-width: 320px) {
  .about {
    position: relative;
  }

  .about__title {
    margin: 55px auto 0 auto;
    width: 288px;
    padding-top: 124px;
    color: #FFFFFF;
    font-size: 28px;
    line-height: 32px;
    font-weight: 500;
    position: relative;
  }
  .about__title::before {
    content: "";
    position: absolute;
    width: 288px;
    height: 3px;
    background-color: #0DD1A2;
    top: 194px;
  }

  .about__description-wrapper {
    margin: 56px auto 0 auto;
    width: 288px;
  }

  .about__description {
    width: 288px;
    font-size: 17px;
    line-height: 25.5px;
    color: #FFFFFF;
  }

  .about__description--second,
.about__description--third {
    margin-top: 25px;
  }
}
@media (min-width: 320px) {
  .about__gallery {
    width: 288px;
    margin: 51px auto 0 auto;
  }

  .about-gallery__title {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
  }

  .about-gallery__images {
    margin-top: 10px;
  }
}
@media (min-width: 320px) {
  .gallery-images__buttons-wrapper {
    display: flex;
    margin-top: -4px;
    margin-left: 120px;
  }

  .gallery-images__counter {
    display: flex;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    font-weight: 500;
    margin-left: -31px;
    margin-top: 14px;
  }

  .gallery-images__counter-max {
    font-weight: 400;
    opacity: 0.8;
  }

  .gallery-images__button-prev {
    margin-left: 23px;
  }

  .gallery-images__button-next::before {
    content: "";
    position: absolute;
    background-color: transparent;
    border-bottom: 1px solid #FFFFFF;
    border-left: 1px solid #FFFFFF;
    width: 8px;
    height: 8px;
    transform: rotate(-135deg);
    top: 20px;
    left: 24px;
  }
}
@media (min-width: 320px) {
  .instruction {
    margin-top: 90px;
  }

  .instruction__title {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 16px;
    color: #FFFFFF;
    font-size: 28px;
    font-weight: 500;
    line-height: 32.17px;
    position: relative;
  }
  .instruction__title::before {
    content: "";
    position: absolute;
    width: 288px;
    height: 3px;
    background-color: #0DD1A2;
    top: 44px;
  }

  .instruction__list {
    padding: 0;
    list-style-type: none;
  }

  .instruction__buttons {
    margin-left: 185px;
    margin-top: 20px;
  }

  .instruction__button-next {
    margin-left: -4px;
  }
}
@media (min-width: 320px) {
  .instruction-item {
    display: none;
    position: relative;
  }

  .instruction-item--visible {
    display: block;
  }

  .instruction-item__wrapper {
    display: flex;
    flex-direction: column;
    margin-top: 68px;
    margin-left: 27px;
  }

  .instruction-item__title {
    margin: 32px 0 0 0;
    font-size: 21px;
    color: #FFFFFF;
    font-weight: 700;
    order: 1;
    width: 117px;
  }

  .instruction-item__image-wrapper {
    order: 0;
    width: 195px;
    height: 130px;
    position: relative;
  }
  .instruction-item__image-wrapper::before {
    content: "";
    width: 195px;
    height: 130px;
    position: absolute;
    background-color: transparent;
    outline: 2px solid #0DD1A2;
    top: -8px;
    left: 39px;
    z-index: 0;
  }
  .instruction-item__image-wrapper::after {
    content: "";
    padding-left: 1px;
    color: #0DD1A2;
    font-size: 28px;
    font-weight: 700;
    width: 40px;
    height: 40px;
    background-color: #FFFFFF;
    position: absolute;
    border-radius: 50%;
    top: 100px;
    left: 20px;
    z-index: 1;
  }

  .instruction-item__image {
    z-index: 1;
    position: relative;
    margin-left: 28px;
  }

  .instruction-item__image-counter {
    font-size: 28px;
    font-weight: 700;
    color: #0DD1A2;
    position: absolute;
    z-index: 2;
    top: 103px;
    left: 32px;
  }

  .instruction-item__text {
    order: 2;
    font-size: 15px;
    color: #FFFFFF;
    line-height: 19.5px;
    width: 256px;
    margin: 9px 0 0 0;
  }

  .instruction-item:nth-of-type(4) .instruction-item__image-counter {
    top: 103px;
    left: 31px;
  }
}
@media (min-width: 320px) {
  .button-prev {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    background-color: #1C1C1C;
    width: 60px;
    height: 48px;
    position: relative;
  }
  .button-prev::before {
    content: "";
    position: absolute;
    background-color: transparent;
    border-bottom: 1px solid #FFFFFF;
    border-left: 1px solid #FFFFFF;
    width: 8px;
    height: 8px;
    transform: rotate(45deg);
    top: 20px;
    left: 28px;
  }

  .button-prev:disabled::before {
    opacity: 0.5;
  }
}
@media (min-width: 320px) {
  .button-next {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    background-color: #1C1C1C;
    width: 60px;
    height: 48px;
    position: relative;
  }
  .button-next::before {
    content: "";
    position: absolute;
    background-color: transparent;
    border-bottom: 1px solid #FFFFFF;
    border-left: 1px solid #FFFFFF;
    width: 8px;
    height: 8px;
    transform: rotate(-135deg);
    top: 20px;
    left: 24px;
  }

  .button-next:disabled::before {
    opacity: 0.5;
  }
}